body {
  margin: 0px;
  width: 100%;
  font-family: 'Crimson Text', serif;
  font-size: 20px;
  background: #fcfcfc;
}

h1 {
  font-family: 'Nunito', sans-serif;
  font-weight: normal;
  font-size: 28px;
  margin: 25px 0px 0px 0px;
  text-transform: lowercase;

}

h2 {
  font-family: 'Nunito', sans-serif;
  font-weight: normal;
  font-size: 32px;
  margin: 15px 0px 35px 0px;
  color: #333;
  word-spacing: 3px;
}

h3 {
  font-family: 'Nunito', sans-serif;
  font-weight: normal;
  font-size: 26px;
  margin: 10px 0px 10px 0px;
  color: #333;
  word-spacing: 2px;
}

h4 {
  font-family: 'Nunito', sans-serif;
  font-weight: normal;
  font-size: 22px;
  margin: 10px 0px 10px 0px;
  color: #333;
  word-spacing: 2px;
}

h5 {
  font-family: 'Nunito', sans-serif;
  font-weight: normal;
  font-size: 18px;
  margin: 10px 0px 10px 0px;
  color: #111;
  word-spacing: 2px;
}

p, li {
  color: #444;
}

a {
  color: #DE3737;
}

.container {
  margin: 0px auto 0px auto;
  width: 960px;
}

#header {
  background: #333;
  width: 100%;
}

#headersub {
  color: #ccc;
  width: 960px;
  margin: 0px auto 0px auto;
  padding: 20px 0px 20px 0px;
}

.chart {
  width: 480px;
}

.lol {
  font-size: 16px;
  color: #888;
  font-style: italic;
}

.sep {
  height: 1px;
  width: 100%;
  background: #999;
  margin: 20px 0px 20px 0px;
}

.footer {
  font-size: 16px;
}

.latex {
  width: 100%;
}

.latex img {
  display: block;
  margin: 0px auto 0px auto;
}

pre {
  font-family: 'Droid Sans Mono';
  font-size: 14px;
}

td img {
  vertical-align: middle;
}

#contents a {
}

.right-floating {
  float: right;
  max-width: 400px;
}

.floating-img {
  max-width: 400px;
}

.center {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.caption {
  font-size: 16px;
}


